import React, { Component, useState, useEffect } from "react";
import './swap.css';
import logo from './logo.png'
import logo1 from './logo1.jpeg'
import {
  contractAddress,
  abi,
  tokenAddres1,
  tokenAbi1,
  tokenAddres2,
  tokenAbi2,
} from "../utils/constant";
import Web3 from 'web3'
function Swap() {
  let accountAd;
  const [account, setAccount] = useState("Connect to Wallet")

  const [showLinks, setShowLinks] = useState(false);
  const [dailyProfit, setdailyProfit] = useState(24);
  const [totalReturn, setTotalReturn] = useState(200);
  const [withdrawn, setwithdrawn] = useState(0);
  const [withdrawAble, setwithdrawAble] = useState(0)
  const [enterAmount, setEnterAmount] = useState();
  const [balance, setbalance] = useState(0);
  const [tangle, setTangle] = useState(true);
  const [dailyreward, setdailyreward] = useState(0);
  const [userdeposit, setuserdeposit] = useState(0)

  const getData = async () => {
    try {
      const web3 = window.web3;
      let contract = new web3.eth.Contract(tokenAbi1, tokenAddres1);
      let users = await contract.methods.balanceOf(accountAd).call();
      console.log("data", users, account, accountAd);
      setbalance(users / (10 ** 9));
    } catch (error) {
      console.log("data", error);
    }
  };

  const getAccounts = async () => {
    const web3 = window.web3;
    try {
      let accounts = await web3.eth.getAccounts();
      setAccount(accounts[0])
      console.log(accounts);
      return accounts;
    } catch (error) {
      console.log("Error while fetching acounts: ", error);
      return null;
    }
  };

  const loadWeb3 = async () => {
    let isConnected = false;
    try {
      if (window.ethereum) {
        window.web3 = new Web3(window.ethereum);
        await window.ethereum.enable();
        isConnected = true;
      } else if (window.web3) {
        window.web3 = new Web3(window.web3.currentProvider);
        isConnected = true;
      } else {
        isConnected = false;
        console.log("Metamask is not installed, please install it on your browser to connect.");
        // alert("Metamask is not installed, please install it on your browser to connect.");
      }
      if (isConnected === true) {
        let accounts = await getAccounts();
        let accountDetails = null;
        setAccount(accounts[0]);
        accountAd = accounts[0];
        window.ethereum.on("accountsChanged", function (accounts) {
        });
        setAccount(accounts[0]);
        accountAd = accounts[0];
        console.log(accounts);
      }
      getData();
    } catch (error) {
      console.log("Error while connecting metamask", error);
      // alert("Error while connecting metamask");
    }
  };

  const enterAmountCall = async (e) => {
    try {

      setEnterAmount(e.target.value);

    } catch (error) {
      console.log("Error while checking locked account", error);
    }
  };

  const maxClc = async (e) => {
    try {

      setEnterAmount(balance);

    } catch (error) {
      console.log("Error while checking locked account", error);
    }
  };

  const approve = async () => {
    const web3 = window.web3;
    try {
      let tokenContract = new web3.eth.Contract(tokenAbi1, tokenAddres1);
      console.log("enterAmount", enterAmount * 10 ** 9);
      let value = enterAmount * 10 ** 9 + "";
      console.log("enterAmount", enterAmount * 10 ** 9);
      await tokenContract.methods.approve(contractAddress, value)
        .send({
          from: account
        })
        .then(async (output) => {
          console.log("response", output);
          setTangle(false);
        }).catch((e) => {
          console.log("response", e);
        });

    } catch (error) {
      console.log("Error while fetching acounts: ", error);

    }
  };

  const swapTokens = async () => {
    const web3 = window.web3;
    try {
      let contract = new web3.eth.Contract(abi, contractAddress);
      // console.log("enterAmount", enterAmount * 10 ** 9);
      let value = enterAmount * 10 ** 9 + "";
      // console.log("enterAmount", enterAmount * 10 ** 9);
      await contract.methods.migrateToV2(enterAmount)
        .send({
          from: account
        })
        .then(async (output) => {
          console.log("response", "transaction is complete");
        }).catch((e) => {
          console.log("response", e);
        });

    } catch (error) {
      console.log("Error while fetching acounts: ", error);

    }
  };

  // useEffect(() => {
  //   setInterval(() => {
  //     loadWeb3();
  //   }, 1000);
  // }, []);

  return (
    <div className="container-fluid">
      <div className="Header">
        <div className="container">
          <div className="row navbar">
            {/* <div className="col-lg  headerimg"> */}
            <div className="navbar-container">
              <img className="logo" src='https://i.ibb.co/QpBJVr5/ape-lab-logo.png' width="100px" alt="logo" />
        
          
              <h2 className="h3 heading-secondary"><b>Ape Lab Migration</b></h2>
          
            
              <button className="connect btn-primary" onClick={loadWeb3}>{account}</button>
              </div>
            {/* </div> */}
          </div>
        </div>
      </div>

      <div className="headerbar">
        <div className="container">
          <div className="row mx-3 row1 custom-row">
            {/* <div className="col-md headerbar2"> */}
            <div id="headerbar">
              <h3 id="h3" className="heading-tertiary"><b>How to migrate?</b></h3>
              <p id="span1" ><b id="span">Step 1:</b> Connect Wallet</p>
              <p id="span1"><b id="span">Step 2:</b> Enter Max tokens (Remove the decimals)</p>
              <p id="span1" ><b id="span">Step 3:</b> Approve to migrate</p>
              <p id="span1" ><b id="span">Step 4:</b> Migrate tokens</p>
              <p id="span1" ><b id="span">❗️</b> After migrating DO NOT send tokens to another wallet or try to add liquidity, the tokens will be burned and you will lose them!</p>
            </div>

            <div className="">
              <div className="card p-3 rounded-3">
                <div>
                  <h5>
                    Exchange Tokens
                  </h5>
                  <p>Trade BULL in an instant</p>
                </div>
                <div className="floatingInputrght">
                  <span >Balance: </span>
                  <span >{balance}</span>
                </div>

                <div className="nputdv">

                  <input min="0" className="form-control" id="floatingInput"
                    placeholder="Enter amount"
                    onChange={enterAmountCall}
                    value={enterAmount}
                  />

                  <button className="connect btn-max" onClick={maxClc}>max</button>

                </div>
                <button href="#" className="btn btn-secondary my-3 py-3 text-white"
                  onClick={approve}>Approve
                </button>

                <button href="#" className="btn btn-secondary my-3 py-3 text-white" disabled={tangle}
                  onClick={swapTokens}>Migrate
                </button>

              </div>
            </div>

          </div>
        </div>
      </div>
    </div>
  );
}

export default Swap;
